@import url('reset.css');
@import url('https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed');

#app {
    font-family: 'Roboto', sans-serif;
}

.container {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    height: auto;
}

header {
    color: #FFFFFF;
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
    background: #3B3E3F;
}

section {
    display: grid;
    grid-template-rows: 1fr auto;
    margin-bottom: 20px;
}

footer {
    padding: 2em;
    text-align: center;
    font-size: .9em;
    background: #3B3E3F;
    color: #DCE1E2;
}
footer a {
    color: #4F8E9D;
}

header p, header h1 {
    color: #DCE1E2;
}

h1 {
    font-size: 1.5em;
    text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif;
}

p {
    line-height: 1.4em;
    padding: 10px 1rem;
}

a {
    text-decoration: none;
}

.logo {
    flex: 1 0 80%;
    padding: 1em;
    display: flex;
}
.logo img {
    height: 40px;
}
.logo h1 {
    font-size: 1.3em;
    margin-left: 10px;
    text-transform: uppercase;
    text-align: left;
    font-family: 'Roboto Condensed', sans-serif;
}
.logo p {
    text-transform: uppercase;
    line-height: 0;
    padding: 10px 0;
}

.hamburger {
    flex: 0 1 20%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; 
}
.bar,
.bar:before,
.bar:after {
    width: 20px;
    padding: 2px;
    background: #FFF;
    border-radius: 2px;
    position: relative;
}
.bar:after {
    content: '';
    position: absolute;
    top: 10px;
    left: 0;
}
.bar:before {
    content: '';
    position: absolute;
    top: -10px;
    left: 0;
}

.navbar {
    position: absolute;
    right: 0;
    top: 100%;
    background: rgba(59, 62, 63, 0.9);
}
.navbar ul {
    width: auto;
    margin: 0 auto;
}
.navbar li {
    border-bottom: 1px solid #76787A;
    cursor: pointer;
}
.navbar li a {
    display: block;
    padding: 20px 10px;
    text-transform: uppercase;
    text-align: left;
    color: #DCE1E2;
} 
.navbar li:last-child {
    border: 0;
}
.swa {
    background: #4F8E9D;
}
.swald {
    background: #FAC41C;
}
.swaee {
    background: #D2681E;
}
.author {
    background: #5E6364;
}


.bookCover {
    padding: 20px 0;
    text-align: center;
}
.bookCover img {
    box-shadow: 0 10px 6px -6px silver;
    border: 1px solid silver;
}

.bookInfo {
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAALElEQVQYV2NkwAJu3779nxFdHCSoqqrKiCIBEwQphksgC8Il0AXBEtgEQRIAyj8XlzswkmcAAAAASUVORK5CYII=') repeat;
    padding: 20px 0;
}
.bookInfo h1 {
    text-align: center;
    margin-bottom: 10px;
}

.reviews {
    padding: 1em;
}
.reviews p {
    font-size: 1.2em;
    font-family: serif;
    font-style: italic;
    font-weight: bold;
}
.reviews span {
    display: block;
    width: 100%;
    text-align: right;
}

#book-1 h1, #book-1 span {
    color: #4F8E9D;
}
#book-2 h1, #book-2 span {
    color: #FAC41C;
}
#book-3 h1, #book-3 span {
    color: #D2681E;
}
#book-author img {
    height: 250px;
    padding: 10px;
    border: 1px solid silver;
}

.details {
    padding: 2em 2em 4em 2em;
    margin-top: 40px;
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAALElEQVQYV2NkwAJu3779nxFdHCSoqqrKiCIBEwQphksgC8Il0AXBEtgEQRIAyj8XlzswkmcAAAAASUVORK5CYII=') repeat;
}
.details p {
    padding: 1em 0;
}
.details strong {
    font-weight: 600;
    color: #3B3E3F;
    font-style: italic;
}
.details a {
    color: #4F8E9D;
}
#booksAll {
    padding: 2em;
    text-align: center;
}
#booksAll img {
    max-height: 200px;
}

@media screen and (min-width: 800px) {

    header {
        height: 100px;
        justify-content: flex-start;
    }

    .logo {
        flex: 1 0 40%;
        text-align: left;
        display: flex;
        align-items: center;
    }
    .logo img {
        height: 60px;
    }
    .logo h1 {
        font-size: 1.5em;
        line-height: 1.3em;
    }

    .navbar {
        flex: 0 1 59%;
        top: 0;
        position: relative;
        height: 100%;
    }
    .navbar ul {
        margin: 0 auto;
        display: flex;
        height: 100%;
    }
    .navbar li {
        width: 30%;
        padding: 0;
        border-bottom: 0;
        border-right: 1px dotted #636769;
    }
    .navbar li a {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        text-transform: uppercase;
        text-align: center;
        padding: 0 5px;
        transition: all .3s;
    }
    .navbar li.author:hover {
        background: #636769;
    }
    .active {
        pointer-events: none;
    }
    section {
        display: block;
    }
    .hamburger {
        display: none;
    }
    .books {
        display: flex;
        justify-content: space-around;
        padding: 40px 0;
    }
    .books img {
        height: 320px;
    }
    #book-1, #book-2, #book-3, #book-author {
        display: flex;
        justify-content: space-between;
    }
    #book-1 .bookInfo > span, 
    #book-2 .bookInfo > span, 
    #book-3 .bookInfo > span {
        display: block;
        width: 100%;
        height: 200px;
        position: relative;
    }
    #book-1 span h1, #book-2 span h1, #book-3 span h1 {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        padding: 10px 20px;
        text-align: left;
        font-weight: bold;
        margin-bottom: 0;
        background-color: rgba(255,255,255, 0.8);
    }
    #book-author h1 {
        text-align: left;
        padding-left: 20px;
        height: 60px;
        line-height: 60px;
        font-weight: bold;
    }
    #book-1 .bookCover, 
    #book-2 .bookCover, 
    #book-3 .bookCover, 
    #book-author .bookCover {
        flex: 1 0 28%;
    }
    #book-1 .bookInfo, 
    #book-2 .bookInfo, 
    #book-3 .bookInfo {
        flex: 0 1 70%;
    }
    #book-1 .bookInfo > span {
        background: url('../../../static/banner-1.png') no-repeat;
        background-size: cover;
    }
    #book-2 .bookInfo > span {
        background: url('../../../static/banner-2.png') no-repeat;
        background-size: cover;
    }
    #book-3 .bookInfo > span {
        background: url('../../../static/banner-3.png') no-repeat;
        background-size: cover;
    }
}

.fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
    opacity: 0;
}


